<template>
  <div class="publicity">
    <bread-nav :nav="nav"></bread-nav>
    <main-part>
      <el-form :model="form" ref="form" label-width="120px" class="form" :rules="rules">
        <el-form-item label="标题：" prop="title">
          <el-input v-model="form.title" style="width: 51%">
          </el-input>
        </el-form-item>
        <el-form-item label="正文内容：" prop="content">
          <wang-editor :content.sync="form.content" v-if="form.content || !id"></wang-editor>
        </el-form-item>
        <el-form-item label="附件：">
          <div class="upload_view">
            <div class="upload_view_list">
            </div>
            <div class="upload_view_button">
              <uploader
                      ref="uploadZone"
                      :options="uploadOptions"
                      :file-status-text="fileStatusText"
                      class="upload_view_btn">
                <uploader-unsupport></uploader-unsupport>
                <uploader-btn :attrs="attrs">上传文件</uploader-btn>
                <div v-for="(item,key) in form.attachmentList" class="item">
                  <span>文件{{key + 1}}：<a :href="item.fileUrl">{{item.fileName}}</a></span>
                  <i @click="removeEnd(key)" class="el-icon-close"></i>
                </div>
                <uploader-list></uploader-list>
              </uploader>
            </div>
          </div>
        </el-form-item>
        <el-form-item label="推送范围：" prop="scopeList">
          <el-input class="input" v-model="selectedDepName.join()" placeholder="请选择范围" style="width: 440px" disabled>
            <el-button @click="showDepDialog" class="hende" slot="append">选择范围</el-button>
            <!--<span @click="" class="hand" slot="append"></span>-->
          </el-input>
        </el-form-item>
      </el-form>
      <!--推送范围弹窗-->
      <el-dialog :visible.sync="depDialog" class="dep-dialog" width="400px">
        <div class="dep-form">
          <p class="title">{{depTitle}}</p>
          <div class="content">
            <item :data="depData" :selectedDep.sync="selectedDep"
                  :selectedDepName.sync="selectedDepName"
            ></item>
          </div>
          <div style="text-align: center; margin-top: 20px">
            <div @click="depDialog = false" class="btn-submit">关闭</div>
          </div>
        </div>
      </el-dialog>
      <div class="btns">
        <div @click="doSubmit()" class="btn-submit">确定</div>
        <div @click="$router.go(-1)" class="btn-clear" style="margin-left: 30px">返回</div>
      </div>
    </main-part>
  </div>
</template>

<script>
  // 复用组织机构中的组件
  import item from '../../school/user/Item'

  export default {
    components: {
      item
    },
    data () {
      // 验证正文内容
      const checkContent = (rule, value, callback) => {
        if (!value || value === '<p><br></p>') {
          callback(new Error('请输入正文内容'))
        } else {
          callback()
        }
      }
      const checkDep = (rule, value, callback) => {
        if (this.selectedDepName.length === 0) {
          callback(new Error('请选择推送范围'))
        } else {
          callback()
        }
      }
      let vm = this
      return {
        id: vm.$route.query.id,
        tabPick: vm.$route.query.tabPick,
        announcementDetail: [],
        nav: [{name: '课题研究', path: 'go(-1)'}, {name: '发布课题研究', path: ''}],
        text: '发布成功',
        depDialog: false,
        // 一级部门名称
        depTitle: '',
        // 部门数据
        depData: [],
        // 选择的部门ID
        selectedDep: [],
        // 选择的部门名称
        selectedDepName: [],
        form: {
          title: null,
          content: null,
          scopeList: [],
          noticeType: 'SUBJECT_STUDY',
          attachmentList: []
        },
        // 附件列表
        attachmentList: [],
        attrs: {
          accept: '*'
        },
        rules: {
          title: {required: true, message: '请输入标题', trigger: 'blur'},
          content: {required: true, validator: checkContent, trigger: 'change'},
          scopeList: {required: true, validator: checkDep, trigger: 'change'},
        },
        uploadOptions: {
          target: `${window.location.origin}${this.$conf.API_PATH}/uploadFileTrunk/trunkUpload.do`,
          testChunks: false,
          chunkSize: 2 * 1024 * 1024,
          singleFile: false,
          parseTimeRemaining: (timeRemaining, parsedTimeRemaining) => {
            return parsedTimeRemaining
              .replace(/\syears?/, '年')
              .replace(/\days?/, '天')
              .replace(/\shours?/, '小时')
              .replace(/\sminutes?/, '分钟')
              .replace(/\sseconds?/, '秒')
          }
        },
        // 修改分片上传文字
        fileStatusText: {
          success: '上传成功',
          error: '上传错误',
          uploading: '正在上传',
          paused: '已暂停',
          waiting: '等待中'
        },
      }
    },
    async mounted () {
      await this.getAllDep()
      this.$nextTick(() => {
        let uploadZone = this.$refs.uploadZone
        if (uploadZone) {
          const uploader = uploadZone.uploader
          uploader.on('fileSuccess', async (rootFile, file, message) => {
            let res = await this.$ajax({
              url: `/uploadFileTrunk/mergeChunks.do`,
              query: {
                fileMd5: file.uniqueIdentifier,
                fileName: file.name
              }
            })
            this.attachmentList.push({
              fileName: file.name,
              fileUrl: res
            })
            console.log(this.attachmentList)
          })
          uploader.on('fileRemoved', async (file) => {
            this.attachmentList.some((e, i) => {
              if (e.name === file.name) {
                this.attachmentList.splice(i, 1)
                return true
              }
            })
          })
        }
      })
      if (this.id) {
        this.getDetail()
      }
    },
    watch: {
      'form.content': function () {
        if (this.id) {
          setTimeout(() => {
            this.$refs.form.validate()
          }, 1)
        }
      },
      'form.title': function () {
        if (this.id) {
          setTimeout(() => {
            this.$refs.form.validate()
          }, 1)
        }
      },
    },
    methods: {
      // 删除已经上传的视频
      removeEnd (key) {
        this.form.attachmentList.splice(key, 1)
        console.log(this.form.attachmentList)
      },
      // 获取所有的部门
      async getAllDep () {
        let res = await this.$ajax({
          url: '/org/queryOrganizationTree.do',
          query: {
            orgTypes: ['ADMIN_ORG', 'TEACH_ORG']
          }
        })
        this.depTitle = res.orgName
        this.depData = recursion(res.subOrgList, 1)

        // 递归给每一个item设置缩进和check-box隐藏
        function recursion (arr, i) {
          arr.forEach(e => {
            e.tier = i
            e.status = false
            if (e.subOrgList.length > 0) {
              recursion(e.subOrgList, i + 1)
            }
          })
          return arr
        }
      },
      // 获取详情
      async getDetail () {
        // console.log(this.id)
        this.announcementDetail = await this.$ajax({
          url: '/notice/getNotice.do',
          el: '.form',
          query: {
            id: this.id
          }
        })
        console.log(this.announcementDetail)
        this.form.title = this.announcementDetail.title
        this.form.content = this.announcementDetail.content
        this.form.noticeType = this.announcementDetail.noticeType
        this.announcementDetail.scopeList.forEach(e => {
          this.selectedDep.push(e.businessId)
          this.selectedDepName.push(e.scopeName)
        })
        // 视频
        this.form.attachmentList = this.announcementDetail.attachmentList
      },
      // 展现选择部门弹窗
      showDepDialog () {
        this.depDialog = true
      },
      // 提交
      async doSubmit () {
        await this.$refs.form.validate()
        // 处理视频
        if (this.attachmentList.length) {
          this.form.attachmentList = this.form.attachmentList.concat(this.attachmentList)

          // console.log(this.attachmentList)
        }
        // 处理所属部门
        this.form.scopeList = this.selectedDep
        // console.log(this.form)
        let url = '/notice/addNotice.do'
        if (this.id) {
          this.text = '修改成功'
          url = '/notice/updateNotice.do'
          this.form.id = this.id
          delete this.form.noticeType
        }
        await this.$ajax({
          url,
          el: '.form',
          query: this.form,
        })
        this.$remind(this.text)
        this.$router.push('/research-orsay/research')
      },
    }
  }
</script>
